html {
  scroll-behavior: smooth;
}

html {
  /*height: 300%;*/
  width: 100%;
  position: relative;
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}

@media (min-width: 624px) {
  html {
    font-size: 16px;
  }
}

@media (max-width: 623px) {
  html {
    font-size: 8px;
  }
}
.first-layer {
    position:relative;
    width: 100%;
    max-height:100.1vh;
    /*overflow: hidden;*/
}


.first-layer-intro {
    position:absolute;
    left: 0;
    right: 0;
    margin-left: auto;
    margin-right: auto;
    width: 85%;
    top:10%;
    text-align: center;
}

.first-layer-text-intro {
    color: #f5ebeb;
    font-weight: bold;
    font-size: 3.5rem;
    line-height: 120%;
    font-family: 'Ruluko', sans-serif;
}

.first-layer-button-intro svg {
    color: #902c68 !important;
    width: 2em !important;
    height: 2em !important;
}

.first-layer-button-intro svg:hover {
    color: deeppink !important;
}


.first-layer-photo{
    width: 100%;
}

.first-layer-picture {
    display: flex !important;
}

.second-layer {
    position:relative;
    width: 100%;
    min-height:100vh;
    background-image:
        radial-gradient(
            snow 10%,
            #e5e9e5 90%
    );
 }

.second-layer-top-floral img {
    position:absolute;
    opacity: 0.2;
    max-width: 100%;
}

.second-layer-bottom-floral img {
    position: absolute;
    bottom: 0;
    right: 0;
    opacity: 0.2;
    max-width: 100%;
}

.second-layer-content {
    position: relative;
    width: 100%;
    height: 100%;
}

.second-layer-content-photos {
    margin: 3% 0 3% 0;
}

.second-layer-text-intro {
    position:relative;
    color: #396d63;
    text-align: center;
    font-weight: bold;
    font-size: 3.5rem;
    line-height: 120%;
    font-family: 'Satisfy', cursive;
}

.second-layer-center {
    color: #396d63;
    text-align: center;
    font-weight: bold;
    font-size: 3rem;
    line-height: 120%;
    font-family: 'Satisfy', cursive;
    margin: auto !important;
    padding: 0.5rem;
}

.second-layer-left-intro {
    position:relative;
    color: white;
    text-align: center;
    font-weight: bold;
    font-size: 2rem;
    line-height: 120%;
    font-family: 'Satisfy', cursive;
    margin-bottom: 1rem;
}


.second-layer-right-intro {
    position:relative;
    color: white;
    text-align: center;
    font-weight: bold;
    font-size: 2rem;
    line-height: 120%;
    font-family: 'Satisfy', cursive;
    margin-bottom: 1rem;
}

.second-layer-left-photo {

}

.second-layer-right-photo {

}

@media (orientation: portrait) {
    .second-layer-left-photo {
        width:45vw;
    }

    .second-layer-right-photo {
        width: 45vw;
    }

    .second-layer-text-intro {
        padding-top: 5%;
    }
}

@media (orientation: landscape) {
    .second-layer-left-photo {
        width:20vw;
    }

    .second-layer-right-photo {
        width: 20vw;
    }

    .second-layer-text-intro {
        padding-top: 5%;
    }
}

.second-layer-text-extra1 {
    position:relative;
    color: #902c68 ;
    text-align: center;
    padding-top:1%;
    font-weight: bold;
    font-size: 4rem;
    line-height: 120%;
    font-family: 'Satisfy', cursive;
}

.second-layer-button-navigation svg {
    color: #902c68 !important;
    width: 2em !important;
    height: 2em !important;
}

.second-layer-button-navigation svg:hover {
    color: deeppink !important;
}

.third-layer {
    position:relative;
    width: 100%;
    max-height:100vh;
    background-color: #F5EBF2;
}

.third-layer-photo {
    width: 100%;
    opacity: 0.8;
}

.third-layer-content {
    position:absolute;
    left: 0;
    right: 0;
    bottom: 0;
    text-align: center;
}

.third-layer-button svg {
    color: #902c68 !important;
    width: 2em !important;
    height: 2em !important;
}

.third-layer-button svg:hover {
    color: deeppink !important;
}

.thrid-layer-picture {
    display: flex !important;
}
.fourth-layer {
    position:relative;
    width: 100%;
    background-color: snow;
    background-image:
            radial-gradient(
                    snow 10%,
                    #e5e9e5 90%
            );
}

.fourth-layer-1, .fourth-layer-2, .fourth-layer-3 {
    width: 100%;
    position: relative;
    top: 0;
    bottom:0;
    text-align: center;
    display: flex;
}

.fourth-layer-church-image {
    max-width: 80%;
    height: auto;
}

.fourth-layer-ballroom-image {
    max-width: 80%;
    height: auto;
}

@media (orientation: portrait) {
    .fourth-layer-1 {
        height: 30vh;
    }
    .fourth-layer-2, .fourth-layer-3 {
        height: 60vh;
    }
}

@media (orientation: landscape) {
    .fourth-layer-1, .fourth-layer-2, .fourth-layer-3 {
        height: 60vh;
    }
}

.fourth-layer-text-intro {
    position:relative;
    color: #396d63;
    text-align: center;
    font-weight: bold;
    font-size: 3.5rem;
    line-height: 120%;
    font-family: 'Satisfy', cursive;
    padding-top: 1rem;
}

.fourth-layer-symbol {
    font-size: 4rem !important;
    margin: auto;
}

.fourth-layer-center-animation {
    margin: auto !important;
    width: -webkit-fit-content !important;
    width: -moz-fit-content !important;
    width: fit-content !important;
    top:0 !important;
}

.fourth-layer-first-part-text, .fourth-layer-second-part-text, .fourth-layer-third-part-text {
    font-family: 'Poppins', sans-serif;
    font-size: 1rem;
}

.fourth-layer-content {
    left: 0;
    right: 0;
    bottom: 0;
    text-align: center;
}
.fifth-layer {
    position:relative;
    display: flow-root;
    width: 100%;
    min-height: 100vh;
    background-color: #f8eced;
}

.fifth-layer-text-intro {
    position:relative;
    color: #396d63;
    text-align: center;
    font-weight: bold;
    font-size: 3.5rem;
    line-height: 120%;
    font-family: 'Satisfy', cursive;
    padding-top: 1rem;
}

.fifth-layer-dialog-paper {
    display: flex;
    flex-wrap: wrap;
    width: 80%;
    margin: 4% auto 4% auto;
}

.fifth-layer-dialog-grid {
    font-family: 'Roboto', sans-serif;
    width: 50%;
    padding: 5%;
}

.MuiFormLabel-root {
    color: #902c68 !important;
    font-weight: bold !important;
}
.footer-layer {
    position:relative;
    width: 100%;
    min-height:50vh;
    background-color: #396d63;
}

.footer-layer-text-container {
    position: relative;
    text-align: center;
    padding-top: 2rem;
}

.footer-layer-text {
    font-weight: bold;
    font-size: 2em;
    color: white;
    font-family: 'Satisfy', cursive;
}

.footer-layer-image-container {
    left:0;
    right:0;
    text-align: center;
    height: 30vh;
    position: relative;
    display: flex;
}

.footer-layer-image {
    width: 26vh;
    height:26vh;
    border-radius: 50%;
    opacity: 0.6;
    margin: auto;
}

.goto-beginning-container {
    position: relative;
    text-align: center;
    left: 0;
    right: 0;
    bottom: 0;
}

.goto-beginning-button svg {
    color: snow !important;
    width: 1.5em !important;
    height: 1.5em !important;
}

.goto-beginning-button svg:hover {
    color: white !important;
}

.footer-bottom-left-text {
    font-family: 'Ruluko', sans-serif;
    color: white;
    font-size: 1.2em;
    padding: 2rem;
    position: absolute;
    bottom: 0;
    left: 0;
    text-align: center;
}

.footer-bottom-right-text {
    font-family: 'Ruluko', sans-serif;
    color: white;
    font-size: 1.2em;
    padding: 2rem;
    position: absolute;
    bottom: 0;
    right: 0;
    text-align: center;
}


